/* ======================================================================== */
/* 22. header */
/* ======================================================================== */
.header
	position: absolute
	top: 0
	left: 0
	right: 0
	z-index: 500
	pointer-events: none
	a, select, input
		pointer-events: initial
	.logo.text-center
		text-align: left !important
.header__controls
	padding-top: 70px
	left: 0
	right: 0
.header_white
	.logo
		color: $white
		&:focus
			color: $white
	.header__burger-line
		background-color: $white
	.menu
		> li a
			color: $white
		> li:not(.menu-item-has-children) > a
			&:before
				background-color: $white
		.sub-menu
			background-color: $dark
.header_fixed
	z-index: unset
	.header__controls
		position: fixed
		z-index: 600
.header_absolute
	z-index: unset
	.header__controls
		position: absolute
		z-index: 600
.header__burger
	position: relative
	display: flex
	flex-direction: column
	align-items: flex-end
	justify-content: center
	width: 40px
	height: 40px
	cursor: pointer
	pointer-events: initial
	z-index: 600
	&:not(.header__burger_opened):hover
		.header__burger-line
			&:nth-child(1)
				transform: scaleX(0.75)
			&:nth-child(2)
				transform: scaleX(1.25)
.header__burger_opened
	.header__burger-line
		background-color: $white
		&:nth-child(1)
			transform: scaleX(0.75)
		&:nth-child(2)
			transform: scaleX(1.25)
	&:hover
		.header__burger-line
			&:nth-child(1)
				transform: scaleX(1)
			&:nth-child(2)
				transform: scaleX(1)
.header__burger-line
	display: block
	width: 100%
	background-color: $black
	height: 2px
	margin: 4px 0
	+trans2
	transform-origin: right center
	transform: translateZ(0)
	&:nth-child(1)
		max-width: 40px
	&:nth-child(2)
		max-width: 30px
.header__wrapper-overlay-menu
	display: flex
	// flex-wrap: wrap
	position: fixed
	top: 0
	left: 0
	right: 0
	bottom: 0
	z-index: 500
	overflow-x: hidden
	overflow-y: auto
	-webkit-overflow-scrolling: touch
	pointer-events: initial
	opacity: 0
	visibility: hidden
.header__wrapper-menu
	display: flex
	align-items: flex-end
	padding: 120px 120px 40px
	width: 100%
.header__wrapper-menu_container
	padding-left: calc(120px + (100vw - 1140px) / 2 )
.header__wrapper-overlay-widgets
	display: flex
	flex-direction: column
	justify-content: flex-end
	flex: 1 1 auto
	padding: 120px 120px 60px
.header__wrapper-property
	margin-bottom: 50px
	font-size: 16px
	&:last-of-type
		margin-bottom: 0
	.widgettitle
		margin-bottom: 1em
		&:before
			display: none
.header__overlay-menu-back
	position: absolute
	top: 70px
	left: 110px
	font-size: 45px !important
	color: $white
	z-index: 600
	cursor: pointer
	pointer-events: initial
	opacity: 0
	visibility: hidden
.header__col-left
	position: relative
	z-index: 600
	// z-index: -2
	// z-index: -1
.header__col-right
	z-index: 600
.header_lock-submenus
	.menu
		.sub-menu
			opacity: 0 !important
			visibility: hidden !important
			transform: translate(0, 10px) !important
	
@media screen and (max-width: $xxl)
	.header__controls
		padding-top: 50px
	.header__wrapper-menu
		padding: 80px 80px 40px
	.header__wrapper-menu_container
		padding-left: calc(80px + (100vw - 1140px) / 2 )
	.header__wrapper-overlay-widgets
		padding: 80px 80px 60px
	.header__overlay-menu-back
		top: 50px
		left: 70px

@media screen and (max-width: $xl)
	.header__overlay-menu-back
		left: 0px	
	.header__wrapper-menu_container
		padding-left: calc(100px + (100vw - 1140px) / 2 )

@media screen and (max-width: $md)
	.header__controls
		padding-top: 40px
	.header__wrapper-menu
		padding: 50px 50px 30px
	.header__wrapper-menu_container
		padding-left: 0
	.header__wrapper-overlay-widgets
		padding: 50px 50px 40px
	.header__wrapper-property
		margin-bottom: 40px
	.header__overlay-menu-back
		top: 40px
		left: 40px
	.header__burger-line
		margin: 3px 0
		&:nth-child(1)
			max-width: 30px
		&:nth-child(2)
			max-width: 25px
	.header__wrapper-menu
		padding: 0
		width: 100%
		padding: 50px 0
		margin-top: auto
		margin-bottom: auto
	.header__wrapper-overlay-menu
		flex-wrap: wrap
		text-align: center
		padding: 50px 30px
	.header__wrapper-overlay-widgets
		border-top: 1px solid $borders-light
		background-color: initial
		padding: 50px 0
	.header__overlay-menu-back
		top: 25px
		left: 15px
		font-size: 33px !important
		
@media screen and (max-width: $md)
	.header__controls
		padding-top: 20px
	.header_fixed
