/* ======================================================================== */
/* 66. table */
/* ======================================================================== */
table
	border-collapse: collapse
	margin: 0 0 24px
	width: calc(100% - 30px)

thead th
	border-bottom: 1px solid $dark
	padding-bottom: 0.5em

th
	padding: 1em
	text-align: left

tr
	border-bottom: 1px solid $borders-dark

td
	padding: 1em

th:first-child, td:first-child
	padding-left: 0

th:last-child, td:last-child
	padding-right: 0
