/* ======================================================================== */
/* 37. postPreview */
/* ======================================================================== */
.post-preview
.post-preview__wrapper-content
	> *:first-child
		margin-top: 0
.post-preview__date
	font-family: $font-secondary
	font-size: 16px
	font-weight: 500
	line-height: 1.5
	letter-spacing: 1px
	text-transform: uppercase
.post-preview__header, .post-preview__media, .post-preview__date
	a
		background-image: none
.post-preview__media
	display: block
	background-image: none
	margin-bottom: 1.5em
	overflow: hidden
	&:hover, &:focus
		opacity: 1
	&:hover
		> *
			transform: scale(1.05)
	> *
		+trans1
.post-preview__header
	// border-bottom: 1px solid $borders-dark
	// margin-bottom: 1em
	h2
		margin-top: 0
		margin-bottom: 0.5em
.post-preview__categories
	+reset-ul
	li
		display: inline-block
.post-preview__meta
	margin-bottom: 1em
	font-size: 14px
.post-preview__date
	// margin-bottom: 2em
.post-preview__wrapper-content
	p
		// font-size: 20px
.post-preview__wrapper-meta
	margin-top: 1em
.post-preview__wrapper-readmore
	text-align: right
	margin-top: 2em

@media screen and (max-width: $md)
	.post-preview__meta
		display: inline-block
		margin: 0.5em
	.post-preview__wrapper-meta
		margin: -0.5em -0.5em 1em
	.post-preview__date
		display: block
		
