/* ======================================================================== */
/* 31. outdated */
/* ======================================================================== */
#outdated
	font-family: sans-serif
	position: absolute
	// background-color: #fdf7d4 !important
	color: white
	display: none
	overflow: hidden
	left: 0
	position: fixed
	text-align: center
	text-transform: uppercase
	top: 0
	width: 100%
	z-index: 100000
	padding: 24px 24px 36px 0

	&.fullscreen
		height: 100%

	.vertical-center
		display: table-cell
		text-align: center
		vertical-align: middle

	h6
		font-size: 25px
		line-height: 25px
		margin: 12px 0
		// color: #b29400 !important

	p
		font-size: 16px
		line-height: 12px
		margin: 0
		// color: #6b5900 !important
		text-transform: none

	#buttonUpdateBrowser
		border: 2px solid white
		// color: #b29400 !important
		cursor: pointer
		display: block
		margin: 30px auto 0
		padding: 16px 24px
		position: relative
		text-decoration: none
		width: 230px
		&:hover
			background-color: #b29400
			color: white

	.last
		height: 20px
		position: absolute
		right: 70px
		top: 10px
		width: auto
		display: inline-table

		&[dir=rtl]
			left: 25px !important
			right: auto !important

	#buttonCloseUpdateBrowser
		color: white
		display: block
		font-size: 36px
		height: 100%
		line-height: 36px
		position: relative
		text-decoration: none
		width: 100%
