/* ======================================================================== */
/* 65. splitText */
/* ======================================================================== */
[data-os-animation]
	.split-text, .split-chars
		opacity: 0
		visibility: hidden
.split-text__line
	// display: inline !important
	overflow: hidden
.split-chars__line
	display: inline
	overflow: hidden

.elementor-editor-active
	[data-os-animation]
		.split-text, .split-chars
			opacity: 1
			visibility: visible
