/* ======================================================================== */
/* 28. logo */
/* ======================================================================== */
.logo
	display: inline-flex
	align-items: center
	color: $black
a.logo:focus
	color: $black
	opacity: 1
a.logo
	background-image: none
.logo__wrapper-img
	margin-right: 15px
	flex: 1 0 auto
	height: 100%
.logo__wrapper-img_no-margin
	margin-right: 0
.logo__text
	margin-top: 0
	margin-bottom: 0
.logo__text-title
	display: block
	font-family: $font-secondary
	font-size: 16px
	line-height: 1.3
	font-weight: 500
	letter-spacing: 1px
	text-transform: uppercase
	line-height: 1 !important
.logo__text-tagline
	display: block
	margin-top: 8px
	font-family: $font-primary
	font-style: normal
	font-size: 10px
	line-height: 1
	font-weight: bold
	letter-spacing: 2px
	text-transform: uppercase
	color: $brown-grey-2
	+trans1
