@media screen and (max-width: $md)
	.grid_120, .grid_100, .grid_50
		margin-left: -15px
		margin-right: -15px
	.grid__item_120, .grid__item_100, .grid__item_50
		padding-left: 15px
		padding-right: 15px
		margin-bottom: 40px
		&:nth-of-type(3)
			margin-top: 0
	.grid__item-icon
		width: 60px
		height: 60px
	.grid__item-icon-symbol
		font-size: 24px !important

@media only screen and (min-width: $md + 1px)
	.grid__item_desktop-12
		flex: 0 0 100%
		max-width: 100%
	.grid__item_desktop-6
		flex: 0 0 50%
		max-width: 50%
	.grid__item_desktop-4
		flex: 0 0 33.333333%
		max-width: 33.333333%
	.grid__item_desktop-3
		flex: 0 0 25%
		max-width: 25%

@media only screen and (min-width: $sm + 1px) and (max-width: $md)
