/* ======================================================================== */
/* 14. filter */
/* ======================================================================== */
.filter
	position: relative
	padding-bottom: 40px
	text-align: center
	z-index: 100
	border-bottom: 2px solid $black
	margin-bottom: 80px
.filter__inner
	position: relative
	padding-left: 15px
	padding-right: 15px
.filter__item
	+small-caps
	padding-top: 10px
	padding-bottom: 10px
	cursor: pointer
	+trans1
	color: $brown-grey-2
	&:not(.filter__item_active):hover
		color: $black
.filter__item_active
	color: $black
.filter__underline
	display: block
	position: absolute
	bottom: 0
	left: 0
	background-color: $black
	width: 0
	height: 1px

@media screen and (max-width: $xl)
	.filter__underline
		display: none
	.filter
		padding-bottom: 20px
		margin-bottom: 40px
