/* ======================================================================== */
/* 30. menu */
/* ======================================================================== */
.menu
	+reset-ul
	word-wrap: normal
	text-align: right
	display: inline-block
	font-size: 14px
	> li
		display: inline-block
		&:not(:last-child)
			margin-right: 17px
		a
			display: block
			// color: $white
			background-image: none
			padding: 7px 15px
			&:hover
				// color: var(--color-accent-primary)
	> li.menu-item-has-children
		> a
			&:after
				content: '\f107'
				font-family: 'FontAwesome'
				display: inline-block
				vertical-align: middle
				margin-left: 5px
				
	> li:not(.menu-item-has-children)
		a
			position: relative
			&:hover
				&:before
					left: 15px
					width: calc(100% - 30px)
			&:before
				content: ''
				position: absolute
				width: 0
				height: 1px
				background-color: $dark
				bottom: 5px
				right: 15px
				+trans1
	.menu-item-has-children
		position: relative
		> a
			&:hover
				~ ul
					opacity: 1
					visibility: visible
					transform: translate(0, 0)

.menu .sub-menu
	position: absolute
	top: 100%
	right: 17px
	border-bottom: 4px solid $dark
	background-color: $white
	+reset-ul
	padding: 15px 0
	opacity: 0
	visibility: hidden
	+trans1
	transform: translate(0, 10px)
	box-shadow: 0 0 40px 0 rgba(0, 0, 0, 0.02)
	> li
		display: block
		white-space: nowrap
		a
			display: flex
			align-items: center
			padding: 10px 30px
			background-image: none
	> li.menu-item-has-children
		> a
			&:after
				content: '\f105'
				font-family: 'FontAwesome'
				display: inline-block
				vertical-align: middle
				margin-left: auto
				padding-left: 15px
	ul
		top: 0
		right: calc(100% + 1px)
		transform: translate(10px, 0)
	&:hover
		opacity: 1
		visibility: visible
		transform: translate(0, 0)

@media screen and (max-width: $xxl)
	.menu
		> li
			a
				padding: 7px
		> li:not(.menu-item-has-children)
			a
				&:hover
					&:before
						left: 7px
						width: calc(100% - 14px)
