/* ======================================================================== */
/* 1. adminBar */
/* ======================================================================== */
// position absolute smooth scroll on
.admin-bar
	.smooth-scroll
		.header__controls,
			top: 32px
			@media screen and (max-width: 782px)
				top: 46px

// position absolute smooth scroll off
.admin-bar
	.header_absolute
		.header__controls,
			top: 32px
			@media screen and (max-width: 782px)
				top: 46px
	.header__overlay-menu-back
		top: 66px
		@media screen and (max-width: 782px)
			top: 24px
