/* ======================================================================== */
/* 23. headerThemes */
/* ======================================================================== */
.header_theme-white
	.menu .sub-menu
		background-color: $light-grey
.header_theme-light-grey
	.menu .sub-menu
		background-color: $white
.header_theme-light
	.menu .sub-menu
		background-color: $white
.header_theme-blue-grey
	.menu .sub-menu
		background-color: #f0f0f0
.header_theme-blue-grey-dark
	.menu .sub-menu
		background-color: $light-blue-grey
.header_theme-dark
	.menu .sub-menu
		background-color: $dark-2
		border-bottom-color: $black
	.widget_polylang
		color: $white
		select
			color: $white
	.wpml-ls-legacy-dropdown a
		color: $white
		&:hover
			color: $white
	.wpml-ls-legacy-list-horizontal
		color: $white

.header_theme-dark-2
	.menu .sub-menu
		background-color: $black
		border-bottom-color: $brown-grey-2
	.widget_polylang
		color: $white
		select
			color: $white
	.wpml-ls-legacy-dropdown a
		color: $white
		&:hover
			color: $white
	.wpml-ls-legacy-list-horizontal
		color: $white

.header_theme-black
	.menu .sub-menu
		background-color: #444
		border-bottom-color: $brown-grey-2
	.widget_polylang
		color: $white
		select
			color: $white
	.wpml-ls-legacy-dropdown a
		color: $white
		&:hover
			color: $white
	.wpml-ls-legacy-list-horizontal
		color: $white

