/* ======================================================================== */
/* 17. footerWidgets */
/* ======================================================================== */
.footer
	.tagcloud
		a
			color: $white
			background-color: $dark-2
			&:hover
				background-color: $black
	.widgettitle
		color: $white
		&:before
			background-color: $white
	.widget
		margin-bottom: 50px
		&:last-child
			margin-bottom: 0
	select
		color: $white
	#wp-calendar td
		color: $brown-grey-2
	.widget_polylang
		color: $white
		select
			color: $white
