/* ======================================================================== */
/* 6. cursor */
/* ======================================================================== */
.cursor
	position: fixed
	top: 0
	left: 0
	user-select: none
	pointer-events: none
	z-index: 10000
	display: none
	mix-blend-mode: exclusion
.cursor__follower
	position: relative
	width: 60px
	height: 60px
	border-radius: 100%
	// border: 1px solid $brown-grey
	// outline: 1px solid $brown-grey
	// overflow: hidden
	svg
		position: absolute
		top: 0
		left: 0
		width: 100%
		height: 100%
		// width: 100%
		// height: 100%
		#inner, #outter
			fill: none
			stroke-linecap: butt
		#inner
			stroke-width: 2px
			stroke: $brown-grey-2
			opacity: .6
		#outter
			stroke-width: 4px
			stroke: $light-blue-grey-2
			// stroke: red
.cursor-progress
	cursor: progress
	*
		cursor: progress
.cursor__arrow
	position: absolute
	top: 0
	bottom: 0
	margin: auto 0
	font-size: 24px !important
	width: 24px
	height: 24px
	opacity: 0
	visibility: hidden
.cursor__arrow_left
	left: 0
.cursor__arrow_right
	right: 0
