/* ======================================================================== */
/* 58. sliderFullscreen */
/* ======================================================================== */
.slider-halfscreen_fullscreen
	height: 100vh
	height: calc(var(--fix-bar-vh, 1vh) * 100)
	flex-direction: column
	.slider-halfscreen__images
		width: 100%
		height: 100%
	.slider-halfscreen__content
		position: absolute
		top: 50%
		transform: translateY(-50%)
		width: 100%
		height: 100%
		max-height: 100%
	.slider-halfscreen__content-inner
		padding: 0 30px
		text-align: center
		h2
			margin-top: 0
			margin-bottom: 0
			width: 100%
			font-size: 68px
	.slider-halfscreen__wrapper-link
		z-index: 50
		left: 0
		right: 0
		text-align: center
		// margin-top: 40px
		// bottom: auto
		// left: auto
		// position: relative
	.slider-halfscreen__link
		display: inline-flex !important
	.slider-halfscreen__header p
		margin-left: auto
		margin-right: auto
		display: block

@media screen and (max-width: $xxl)
	.slider-halfscreen_fullscreen
		.slider-halfscreen__content-inner
			h2
				font-size: 54px
				
@media screen and (max-width: $md)
	.slider-halfscreen_fullscreen
		.slider-halfscreen__content
			height: auto
		.slider-halfscreen__content-inner
			h2
				font-size: 28px
				line-height: 1.3
