@function rem($size) {
  $remSize: $size / $em-base;
  @return #{$remSize}rem; }
@mixin trans1 {
  transition: all 0.4s cubic-bezier(0.785, 0.135, 0.150, 0.860); }
@mixin trans2 {
  transition: all 0.6s cubic-bezier(0.785, 0.135, 0.150, 0.860); }
@mixin reset-ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

@mixin small-caps {
  font-family: $font-secondary;
  font-size: 13px;
  line-height: 1.3;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
  @media only screen and (max-width: 991px) {
    font-size: 11px;
    letter-spacing: 0.8px; } }

@mixin link-underline {
  &:after, &:before {
    content: '';
    position: absolute;
    bottom: 0;
    width: 0;
    right: 0;
    height: 1px;
    background-color: $white; }
  &:before {
    transition: width .4s cubic-bezier(0.51, 0.18, 0, 0.88) .1s; }
  &:after {
    transition: width .2s cubic-bezier(0.29, 0.18, 0.26, 0.83); }
  &:after, &:before {
    width: calc(100% - 10px);
    left: 0; } }

@mixin material-icons {
  font-family: 'Material Icons';
  font-style: normal;
  letter-spacing: normal;
  text-transform: none;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  font-feature-settings: 'liga';
  ms-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility; }

$em-base: 16px;
$font-primary: var(--font-primary);
$font-secondary: var(--font-secondary);

// Color Palette
$black: #333333;
$dark: #181818;
$dark-1: #1f1f1f;
$dark-2: #232323;
$black0: #0f0f0f;
$black1: #121212;
$white: #ffffff;
$light-grey: #f9f9f9;
$very-light-pink: #f2f2f2;
$light-blue-grey: #d6d7d9;
$light-blue-grey-2: #c5c6c9;
$brown-grey: #b0b0b0;
$brown-grey-2: #888888;
$borders-light: rgba(214, 215, 217, .2);
$borders-dark: rgba(176, 176, 176, .3);

// Responsive Breakpoints
$xxl: 1400px;
$xl: 1280px;
$lg: 1024px;
$md: 991px;
$sm: 767px;
$xs: 576px;

html {
	font-size: $em-base;
	-webkit-font-smoothing: antialiased;
	text-rendering: optimizeSpeed;
	-webkit-text-rendering: optimizeSpeed;
	-moz-osx-font-smoothing: grayscale;
	font-kerning: none;
	overflow-x: hidden;
	width: 100%; }
body {
	width: 100%;
	min-width: 320px;
	font-family: $font-primary;
	background-color: $light-blue-grey-2;
	color: $black;
	font-size: 100%;
	line-height: 1.75;
	overflow-x: hidden;
	word-wrap: break-word; }
.body_lock-scroll {
	overflow-y: hidden;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	&.admin-bar *:not(.smooth-scroll) {
		.header__controls {
			top: 32px;
			@media screen and (max-width: 782px) {
				top: 46px; } } } }
.body_custom-cursor {}
iframe {
	max-width: 100%; }
img {
	max-width: 100%;
	height: auto; }
.container-fluid {
	padding-left: 120px;
	padding-right: 120px; }
.container {
	padding-left: 25px;
	padding-right: 25px; }
.row {
	margin-left: -25px;
	margin-right: -25px; }
.col, .container [class*='col-'], .container-fluid [class*='col-'] {
	padding-left: 25px;
	padding-right: 25px; }
.no-gutters {
	margin-left: 0 !important;
	margin-right: 0 !important; }
.page-wrapper {
	min-height: calc(var(--fix-bar-vh, 1vh) * 100);
	z-index: 50; }
.page-wrapper_hidden {
	opacity: 0;
	visibility: hidden; }
[data-barba='wrapper'] {
	position: relative;
	z-index: 50;
	min-height: calc(var(--fix-bar-vh, 1vh) * 100); }
#js-webgl {
	display: none; }

@media screen and (max-width: $xxl) {
	.container-fluid {
		padding-left: 80px;
		padding-right: 80px; } }
@media screen and (max-width: $xl) {
	.container-fluid {
		padding-left: 50px;
		padding-right: 50px; } }
@media screen and (max-width: $md) {
	.container-fluid, .container {
		padding-left: 20px;
		padding-right: 20px; }
	.row {
		margin-left: -15px;
		margin-right: -15px; }
	.col, .container [class*='col-'], .container-fluid [class*='col-'] {
		padding-left: 15px;
		padding-right: 15px; } }

/* ======================================================================== */
/* 1. adminBar */
/* ======================================================================== */
// position absolute smooth scroll on
.admin-bar {
	.smooth-scroll {
		.header__controls, {
			top: 32px;
			@media screen and (max-width: 782px) {
				top: 46px; } } } }

// position absolute smooth scroll off
.admin-bar {
	.header_absolute {
		.header__controls, {
			top: 32px;
			@media screen and (max-width: 782px) {
				top: 46px; } } }
	.header__overlay-menu-back {
		top: 66px;
		@media screen and (max-width: 782px) {
			top: 24px; } } }

/* ======================================================================== */
/* 2. asideCounters */
/* ======================================================================== */
.aside-counters {}
.aside-counters__wrapper-item {
	position: relative;
	margin-bottom: 60px;
	margin-top: 60px;
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 10px;
		bottom: 10px;
		right: 0;
		width: 1px;
		background-color: $borders-dark;
		transform: rotate(30deg); }
	&:nth-child(3n) {
		&:after {
			display: none; } }
	&:last-child {
		&:after {
			display: none; } } }

@media only screen and (max-width: $xs) {
	.aside-counters__wrapper-item {
		&:after {
			transform: none;
			right: 15px;
			left: 15px;
			top: auto;
			bottom: -40px;
			width: auto;
			// width: 100%
			height: 1px; }
		&:nth-child(3n) {
			&:after {
				display: block; } }
		&:last-child {
			&:after {
				display: none; } } }
	.aside-counters__wrapper-item {
		margin-bottom: 40px;
		margin-top: 40px; } }

/* ======================================================================== */
/* 3. button */
/* ======================================================================== */
.button {
	position: relative;
	display: inline-block;
	padding: 22px 50px;
	border-radius: 100px;
	border-style: solid;
	border-width: 2px;
	border-color: transparent;
	@include small-caps;
	text-align: center;
	@include trans1;
	cursor: pointer;
	background-image: none;
	&:focus {
		outline: none; }
	&:hover, &:focus {
		opacity: 1;
		transform: translateY(-3px); } }
.button_bordered {
	&.button_dark {
		border-color: $dark;
		color: $dark;
		&:focus {
			color: $dark; }
		&:hover {
			color: $dark; } }
	&.button_light {
		border-color: $light-grey;
		color: $light-grey;
		&:focus {
			color: $light-grey; }
		&:hover {
			color: $dark; } }
	&.button_black {
		border-color: $black;
		color: $black;
		&:focus {
			color: $black; }
		&:hover {
			color: $white; } }
	&.button_white {
		border-color: $white;
		color: $white;
		&:focus {
			color: $white; }
		&:hover {
			color: $dark;
			background-color: $white; } } }
.button_solid {
	border-color: transparent;
	&.button_dark {
		background-color: $dark;
		color: $white;
		&:focus {
			color: $white; }
		&:hover {
			border-color: $dark; } }
	&.button_light {
		background-color: $light-grey;
		color: $dark;
		&:focus {
			color: $dark; }
		&:hover {
			border-color: $light-grey; } }
	&.button_black {
		background-color: $black;
		color: $white;
		&:focus {
			color: $white; }
		&:hover {
			border-color: $dark; } }
	&.button_white {
		background-color: $white;
		color: $dark;
		&:focus {
			color: $dark; }
		&:hover {
			border-color: $dark;
			background-color: $dark;
			color: $white; } } }
.button_fullwidth {
	width: 100%; }
.button_small {
	padding: 16px 32px; }

/* ======================================================================== */
/* 4. backgrounds */
/* ======================================================================== */
.bg-white {
	background-color: $white; }
.bg-light {
	background-color: $very-light-pink;
	.slider-header-testimonials {
		&:before {
			background: linear-gradient(-90deg, rgba(242, 242, 242, 0) 0%, rgba(242, 242, 242, 1) 100%); }
		&:after {
			background: linear-gradient(90deg, rgba(242, 242, 242, 0) 0%, rgba(242, 242, 242, 1) 100%); } } }
.bg-light-grey {
	background-color: $light-grey; }
.bg-blue-grey {
	background-color: $light-blue-grey; }
.bg-blue-grey-dark {
	background-color: $light-blue-grey-2;
	.aside-counters__wrapper-item {
		&:after {
			background-color: $brown-grey; } } }
.bg-dark {
	background-color: $dark; }
.bg-dark-2 {
	background-color: $dark-2; }
.bg-black {
	background-color: $black; }
.bg-black-0 {
	background-color: $black0; }
.bg-black-1 {
	background-color: $black1; }
.color-white {
	.slider__caption {
		color: $white; }
	.slider__counter_current {
		color: $white; }
	.slider__progressbar {
		background-color: $brown-grey-2; }
	.slider__progressbar-fill {
		background-color: $white; }
	.figure-property__headline {
		background-color: $white; }
	.section-intro__headline {
		background-color: $white; }
	.section__headline {
		background-color: $white; }
	.slider {
		.slider__dot {
			&:before {
				background-color: $brown-grey; }
			&:after {
				border-color: $white; } }
		.slider__dot_active {
			&:before {
				background-color: $white;
				border-color: $white; } }
		.slider-text__line {
			background-color: $borders-dark; }
		.slider-halfscreen__link-line {
			background-color: $white; } } }

@for $i from 1 through 9 {
	.bg-image-#{$i} {
		background-image: url('../img/assets/projects/bg-#{$i}.jpg'); } }
div[class*='bg-image-'] {
	background-size: cover;
	background-position: center center; }

