.comment-list {
	@include reset-ul;
	padding-left: 0 !important;
	> li {
		padding-top: 30px;
		padding-bottom: 30px;
		ol.children {
			@include reset-ul;
			padding-left: 4%;
			li {
				padding-top: 30px;
				padding-bottom: 30px; }
			li:last-child {
				padding-bottom: 0; } } }
	> li:not(:last-child) {
		border-bottom: 1px solid $borders-dark; }
	> ol {
		@include reset-ul; } }
.comment-author {
	width: 100%;
	max-width: 80px;
	margin-right: 30px;
	.avatar {
		position: relative;
		width: 100%;
		max-width: 80px;
		max-height: 80px;
		border-radius: 100%;
		display: block;
		@media only screen and (max-width: $md) {
			width: 50px;
			height: 50px; } } }

.comment-meta {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 0.5em;
	a {
		background-image: none; }
	@media only screen and (max-width: $md) {
		flex-wrap: wrap; } }

.comment-metadata {
	@include small-caps;
	margin-left: 10px;
	a:not(.comment-edit-link) {
		color: $brown-grey;
		&:hover {
			color: var(--color-accent-secondary); } }
	a {
		border-bottom: none !important; }
	@media only screen and (max-width: $md) {
		margin-top: 5px;
		margin-left: 0; } }
.comment-body {
	display: flex;
	.fn {
		font-size: 18px;
		font-weight: 600;
		line-height: 1.44;
		color: $dark;
		a {
			border-bottom: none; } } }
.comment-author {
	flex: 0 0 auto; }
.comment-content {
	width: 100%; }
.reply {
	display: inline-block;
 }	// margin-top: 10px
.comment-reply-link {
	@include small-caps;
	border-bottom: none !important;
	margin-right: 24px;
	background-image: none;
	&:before {
		content: '\f112';
		font-family: 'FontAwesome';
		text-transform: none;
		margin-right: 8px; } }
.comment-respond {
	margin-top: 30px; }
.comment-edit-link {
	@include small-caps;
	color: var(--color-accent-primary);
	border-bottom: none !important;
	background-image: none;
	&:before {
		content: '\f044';
		font-family: 'FontAwesome';
		text-transform: none;
		margin-right: 6px; } }
.url {
	background-image: none; }


/* ======================================================================== */
/* 6. cursor */
/* ======================================================================== */
.cursor {
	position: fixed;
	top: 0;
	left: 0;
	user-select: none;
	pointer-events: none;
	z-index: 10000;
	display: none;
	mix-blend-mode: exclusion; }
.cursor__follower {
	position: relative;
	width: 60px;
	height: 60px;
	border-radius: 100%;
	// border: 1px solid $brown-grey
	// outline: 1px solid $brown-grey
	// overflow: hidden
	svg {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		// width: 100%
		// height: 100%
		#inner, #outter {
			fill: none;
			stroke-linecap: butt; }
		#inner {
			stroke-width: 2px;
			stroke: $brown-grey-2;
			opacity: .6; }
		#outter {
			stroke-width: 4px;
			stroke: $light-blue-grey-2;
 } } }			// stroke: red
.cursor-progress {
	cursor: progress;
	* {
		cursor: progress; } }
.cursor__arrow {
	position: absolute;
	top: 0;
	bottom: 0;
	margin: auto 0;
	font-size: 24px !important;
	width: 24px;
	height: 24px;
	opacity: 0;
	visibility: hidden; }
.cursor__arrow_left {
	left: 0; }
.cursor__arrow_right {
	right: 0; }

/* ======================================================================== */
/* 7. counter */
/* ======================================================================== */
.counter {
	text-align: center; }
.counter__number {
	font-size: 68px;
	font-weight: 200;
	line-height: 1; }
.counter__label {
	margin-top: 5px; }

// @media only screen and (max-width: $xl)
// 	.counter__number
@media screen and (max-width: $xxl) {
  .counter__number {
    font-size: 54px; } }
@media screen and (max-width: $md) {
  .counter__number {
    font-size: 43px; } }

/* ======================================================================== */
/* 8. elementor */
/* ======================================================================== */
.elementor-lightbox .dialog-widget-content {
	top: 0 !important;
	left: 0 !important;
	width: 100% !important; }
a.elementor-post__thumbnail__link {
	background-image: none; }
a.elementor-gallery-item {
	background-image: none; }

/* ======================================================================== */
/* 9. figureMember */
/* ======================================================================== */
.figure-member {
	position: relative; }
.figure-member_has-social {
	&:hover {
		.figure-member__wrapper-img img {
			transform: scale(1.1); }
		.figure-member__overlay {
			transform: scaleX(1); }
		.figure-member__social {
			li {
				transform: translateX(0px);
				opacity: 1;
				visibility: visible;
				@for $i from 1 through 10 {
					&:nth-child(#{$i}) {
						transition-delay: 50ms + $i * 50ms; } } } } } }
.figure-member__content {
	text-align: right;
	margin-top: 1em; }
.figure-member__position {
	@include small-caps;
	position: absolute;
	top: -7px;
	right: calc(100% + 20px);
	color: $brown-grey-2;
	transform: rotate(-90deg);
	transform-origin: right center;
	text-align: right;
	white-space: nowrap; }
.figure-member__wrapper-img {
	position: relative;
	overflow: hidden;
	img {
		width: 100%;
		@include trans1;
		transform-origin: center center; } }
.figure-member__overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $dark;
	@include trans1;
	transform: scaleX(0);
	transform-origin: right center; }
.figure-member__social {
	position: absolute;
	top: 30px;
	left: 30px;
	z-index: 50;
	color: $white;
	li {
		display: block;
		transform: translateX(30px);
		transition: all 0.6s ease;
		opacity: 0;
		visibility: hidden;
		margin-right: 0;
		margin-bottom: 15px; } }

@media screen and (max-width: $xxl) {
	.figure-member__position {
		font-size: 12px; } }
@media screen and (max-width: $md) {
	.figure-member {
		text-align: center; }
	.figure-member__content {
		text-align: center;
		margin-top: 20px;
		h4 {
			margin-bottom: 0; } }
	.figure-member__position {
		position: relative;
		top: auto;
		left: auto;
		right: auto;
		bottom: auto;
		transform: none;
		text-align: center;
		margin-bottom: 10px; } }

/* ======================================================================== */
/* 10. figureLogo */
/* ======================================================================== */
.figure-logo {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 330px;
	height: 330px;
	padding: 30px;
	img {
	 @include trans1; }
	&:hover {
		.figure-logo__description {
			transform: scaleY(1);
			p {
				transform: translateY(0px);
				opacity: 1;
				visibility: visible;
				transition-delay: 0.15s; }
			.figure-logo__line {
				transition-delay: 0.2s;
				transform: scaleX(1); } }
		img {
			transform: translateY(-15px); } } }
.figure-logo__description {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: $dark;
	color: $white;
	@include trans1;
	transform: scaleY(0);
	transform-origin: bottom center;
	p {
		opacity: 0;
		visibility: hidden;
		transform: translateY(10px);
		@include trans1; } }
.figure-logo__line {
	display: inline-block;
	width: 30px;
	height: 1px;
	background-color: $white;
	transform: scaleX(0);
	@include trans1; }

@media screen and (max-width: $xl) {
	.figure-logo {
		width: 270px;
		height: 270px;
		max-width: 100%; } }
@media screen and (max-width: $md) {
	.figure-logo {
		width: 100%; } }

/* ======================================================================== */
/* 11. figureImage */
/* ======================================================================== */
.figure-image {
	text-align: center;
	img {
		width: 100%; }
	h5 {
		display: block;
		width: 100%;
		margin-top: 1em;
		margin-bottom: 1em; } }
.figure-image__wrapper-img {
	position: relative;
	overflow: hidden; }

/* ======================================================================== */
/* 12. figurePricing */
/* ======================================================================== */
.figure-pricing {
	position: relative;
	display: flex;
	flex-direction: column;
	padding: 50px;
	position: relative;
	height: 100%;
	z-index: 50;
	background-color: $very-light-pink; }
.figure-pricing_featured {
	background-color: $dark;
	color: $white;
	.figure-pricing__header {
		border-color: $white; }
