			border: 1px solid $brown-grey;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			transform: scale(0);
			@include trans1; }
		&:hover {
			opacity: 1;
			&:after {
				transform: scale(1); } } } }

@media screen and (max-width: $md) {
	.social__item a {
		width: 40px;
		height: 40px;
		font-size: 14px; } }

/* ======================================================================== */
/* 64. spinner */
/* ======================================================================== */
$offset: 202;
$duration: 1.2s;

.spinner {
	position: fixed;
	left: 120px;
	bottom: calc(var(--fix-bar-vh, 30px) + 15px);
	width: 30px;
	height: 30px;
	z-index: 10000;
	animation: rotator $duration ease-in-out infinite;
	opacity: 0;
	visibility: hidden; }
@keyframes rotator {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(270deg); } }

.spinner__path {
	stroke: $brown-grey;
	stroke-dasharray: $offset;
	stroke-dashoffset: 0;
	transform-origin: center;
	animation: dash $duration ease-in-out infinite; }

@keyframes dash {
	0% {
		stroke-dashoffset: $offset;
		transform: rotate(0deg); }
	50% {
		stroke-dashoffset: $offset / 4;
		transform: rotate(135deg); }
	100% {
		stroke-dashoffset: $offset;
		transform: rotate(450deg); } }

@media screen and (max-width: $xxl) {
	.spinner {
		left: 80px; } }
@media screen and (max-width: $xl) {
	.spinner {
		left: 30px; } }
@media screen and (max-width: $md) {
	.spinner {
		left: 15px; } }

/* ======================================================================== */
/* 65. splitText */
/* ======================================================================== */
[data-os-animation] {
	.split-text, .split-chars {
		opacity: 0;
		visibility: hidden; } }
.split-text__line {
	// display: inline !important
	overflow: hidden; }
.split-chars__line {
	display: inline;
	overflow: hidden; }

.elementor-editor-active {
	[data-os-animation] {
		.split-text, .split-chars {
			opacity: 1;
			visibility: visible; } } }

/* ======================================================================== */
/* 66. table */
/* ======================================================================== */
table {
	border-collapse: collapse;
	margin: 0 0 24px;
	width: calc(100% - 30px); }

thead th {
	border-bottom: 1px solid $dark;
	padding-bottom: 0.5em; }

th {
	padding: 1em;
	text-align: left; }

tr {
	border-bottom: 1px solid $borders-dark; }

td {
	padding: 1em; }

th:first-child, td:first-child {
	padding-left: 0; }

th:last-child, td:last-child {
