html
	font-size: $em-base
	-webkit-font-smoothing: antialiased
	text-rendering: optimizeSpeed
	-webkit-text-rendering: optimizeSpeed
	-moz-osx-font-smoothing: grayscale
	font-kerning: none
	overflow-x: hidden
	width: 100%
body
	width: 100%
	min-width: 320px
	font-family: $font-primary
	background-color: $light-blue-grey-2
	color: $black
	font-size: 100%
	line-height: 1.75
	overflow-x: hidden
	word-wrap: break-word
.body_lock-scroll
	overflow-y: hidden
	position: fixed
	top: 0
	right: 0
	bottom: 0
	left: 0
	&.admin-bar *:not(.smooth-scroll)
		.header__controls
			top: 32px
			@media screen and (max-width: 782px)
				top: 46px
.body_custom-cursor
iframe
	max-width: 100%
img
	max-width: 100%
	height: auto
.container-fluid
	padding-left: 120px
	padding-right: 120px
.container
	padding-left: 25px
	padding-right: 25px
.row
	margin-left: -25px
	margin-right: -25px
.col, .container [class*='col-'], .container-fluid [class*='col-']
	padding-left: 25px
	padding-right: 25px
.no-gutters
	margin-left: 0 !important
	margin-right: 0 !important
.page-wrapper
	min-height: calc(var(--fix-bar-vh, 1vh) * 100)
	z-index: 50
.page-wrapper_hidden
	opacity: 0
	visibility: hidden
[data-barba='wrapper']
	position: relative
	z-index: 50
	min-height: calc(var(--fix-bar-vh, 1vh) * 100)
#js-webgl
	display: none
	
@media screen and (max-width: $xxl)
	.container-fluid
		padding-left: 80px
		padding-right: 80px
@media screen and (max-width: $xl)
	.container-fluid
		padding-left: 50px
		padding-right: 50px
@media screen and (max-width: $md)
	.container-fluid, .container
		padding-left: 20px
		padding-right: 20px
	.row
		margin-left: -15px
		margin-right: -15px
	.col, .container [class*='col-'], .container-fluid [class*='col-']
		padding-left: 15px
		padding-right: 15px
